
/*

NOTE:

When loading WP content list we MUST load the whole contents list because WP does not order the list properly.
As this is the case then the code will check that everything has loaded and through an error if not.

*/


import { getData } from '../../helpers/getData.js'

export const loadWPcontentsList = {
  namespaced: true,

  test() {
    console.log("huh")
  },

  state: {
    iam: "loadWPcontents",
    "maxPagesToLoad": 20, //here so that we can fine tune later
    "resultsPerPage": 30,
    "pages": [],
    "functionToCall": undefined,
    "functionToCallContext": undefined
  },


  //valid data check AND wordpress loaded data is reformatted to expected structure (maintains link to original data)
  checkAndReFormatLoadedData(data) {
    //console.log("reFormatLoadedData")
    //debugger // eslint-disable-line no-debugger
    var obj, formattedObj
    for (var i in data) {
      obj = data[i]
      
      if (obj.id && obj.title.rendered) {
        //"id":1, "label":"testxsx", hidden: false, "expanded": true
        formattedObj={id:obj.id, label:obj.title.rendered, isSelected:false, menu_order:obj.menu_order, parent:obj.parent, hidden:false, showChildren:false, expanded:false, origData:obj}
        data[i]=formattedObj
      }
    }
    //debugger // eslint-disable-line no-debugger
    return data
  },

  dataLoaded(obj) {
    if (obj) {
      
      if (obj.error) {
        debugger // eslint-disable-line no-debugger
        loadWPcontentsList.state.functionToCall(obj)
      } else {
        //loadWPcontentsList.state.pages=obj.data

        var dat=obj.data
        //reformat before we properly order the array - just to make things a bit quicker! 
        //debugger // eslint-disable-line no-debugger
        dat=loadWPcontentsList.checkAndReFormatLoadedData(dat)

        //sometimes items will have the same menu_order - in which case they must be sorted by id - so we do this here because it only needs doing once!
        dat=loadWPcontentsList.orderByID(dat);

        //debugger // eslint-disable-line no-debugger
        //need to reorder pages based on parent, menu_order, id
        //loadWPcontentsList.state.pages=loadWPcontentsList.pageWalk(obj.data)
        loadWPcontentsList.state.pages=loadWPcontentsList.pageWalk(dat)

        //debugger // eslint-disable-line no-debugger
        obj={pages:loadWPcontentsList.state.pages, thereIsMore:obj.thereIsMore}
        
        loadWPcontentsList.state.functionToCall(obj, loadWPcontentsList.state.functionToCallContext )
      }
    }
  },



  pageWalk(data) {
    //console.log("pageWalk")
    //debugger // eslint-disable-line no-debugger
    var ordered=this.getLevel(data, 0)
    //order level 0
    ordered=this.orderByMenuOrder(ordered);
    //console.log(ordered)
    
    //debugger // eslint-disable-line no-debugger
    

    //this.state.pages=ordered
    //this.state.pages.push(...ordered)
    
    
    //this.commit("correctPageOrdering2")
    
    for (var i=0;i<ordered.length;i++) {
      //debugger // eslint-disable-line no-debugger
      ordered[i]=this.getChildren(ordered[i], data, -1)

    }
    //debugger // eslint-disable-line no-debugger
    return ordered
  },


  getChildren(dat, allData, currentDepth) {
    //debugger // eslint-disable-line no-debugger
    currentDepth++;
    //console.log("getChildren:" + dat.id + " :: " + currentDepth + " :: " + dat.label);
    //debugger // eslint-disable-line no-debugger
    var children=this.getLevel(allData, dat.id);
    children=this.orderByMenuOrder(children);
    if (children.length>0) {
      //console.log("has");
      //debugger // eslint-disable-line no-debugger
      for (var i=0;i<children.length;i++) {
        children[i]=this.getChildren(children[i], allData, currentDepth)
        //console.log("child:" + children[i].id)
      }
    } else {
      //console.log("none");
    }
    //debugger // eslint-disable-line no-debugger
    dat.children=children;
    return dat;
    //console.log(dat + allData + currentDepth);
  },


  formatLoadedData(obj) {
    if (obj) {
      
      //loadWPcontentsList.state.pages=obj.data

      var dat=obj.data
      //reformat before we properly order the array - just to make things a bit quicker! 
      //debugger // eslint-disable-line no-debugger
      dat=loadWPcontentsList.checkAndReFormatLoadedData(dat)

      //sometimes items will have the same menu_order - in which case they must be sorted by id - so we do this here because it only needs doing once!
      dat=loadWPcontentsList.orderByID(dat);

      //debugger // eslint-disable-line no-debugger
      //need to reorder pages based on parent, menu_order, id
      //loadWPcontentsList.state.pages=loadWPcontentsList.pageWalk(obj.data)
      return loadWPcontentsList.pageWalk(dat)

      //debugger // eslint-disable-line no-debugger
      
      //loadWPcontentsList.state.functionToCall(obj, loadWPcontentsList.state.functionToCallContext )
    }
  },


  
  async loadWPcontents(url) {
    //console.log("loadWPcontents: "+url)
    
    var errored="";
    var dat;
    await getData.loadPagedRecords(url, 1, this.state.maxPagesToLoad, this.state.resultsPerPage, this.dataLoaded).then(
      function (ret) {
        //debugger // eslint-disable-line no-debugger
        if (ret.status==="ok") {
          //replicate dataLoaded
          //debugger // eslint-disable-line no-debugger  
          dat=loadWPcontentsList.formatLoadedData(ret);
          
        } else {
          errored="bad return from loadPagedRecords"
        }
      },
      function (err) {
        //debugger // eslint-disable-line no-debugger
        if (err) {
          console.log("Error: " + err)
          errored=err
        } else {
          errored="undefined error in loadWPcontents"  
        }
      }
    )

    return new Promise(function(loaded, failed) {
      if (errored==="") {
        //debugger // eslint-disable-line no-debugger
        loaded(dat)
      } else {
        //debugger // eslint-disable-line no-debugger
        failed(errored)
      }
    })

  },

  wait3() {
    return new Promise((resolve, reject) => {
      console.log("....wait3 start")
      setTimeout(() => {
        console.log("....wait3 done")
        debugger // eslint-disable-line no-debugger
        resolve("3 second wait done")
      }, 3000)
      if (reject) {
        console.log("x")
      }
    })
  },  

  modules: {
    getData: getData,
  },



  /* format the data that has been loaded */

  getLevel(arr, id) {
    var ret=[]
    var len=arr.length;
    for (var i=0;i<len;i++) {
      if (arr[i].parent===id) {
        ret.push(...arr.splice(i,1))
        len--;i--;
      }
    }
    return ret
  },
  orderByID(arr) {
    arr.sort(function(a, b){
      //debugger // eslint-disable-line no-debugger
      if (a.id < b.id) {return -1;}
      if (a.id > b.id) {return 1;}
      return 0;
    });
    return arr;
  },
  orderByMenuOrder(arr) {
    arr.sort(function(a, b){
      //debugger // eslint-disable-line no-debugger
      if (a.menu_order < b.menu_order) {return -1;}
      if (a.menu_order > b.menu_order) {return 1;}
      return 0;
    });
    return arr;
  },  


}






