    isDebugVisible(state) {
      //debugger // eslint-disable-line no-debugger
      if (state.app.debug && state.app.allowDebugScreen) {
        return {
          debugView: state.app.debugIsVisible,
          debugHidden: !state.app.debugIsVisible 
        }
      } else {
          return {
            debugView: false,
            hidden: true 
          }
      }
    },
    isMessageScreenVisible(state) {
