import { mapState } from 'vuex'
import store from '../../../store'
//import getData from './getData.js'
//import { getData } from './helpers/getData.js'
import { reactive } from 'vue'

import { loadWPcontentsList } from './loadWPcontentsList.js'

export const WPnavMenu= {
  namespaced: true,
  setup() {
    /*
    const unsubscribe = store.subscribe((mutation, state) => {
      console.log(mutation.type)
      console.log(mutation.payload)
    })
    */
  },
  state: {
    
    "iam": "WPnavMenu.js",
    "wordPressSite":"",
    "pagesEndpoint": "", //moved up a level because some sites behave differently!

    "resultsPerPage": 100,

    "pages": [],
    "expandedIds": [],
    /* staticTree is the data structure that this works with - so reformat any external/incoming data to fit this pattern! */
    /* NB! this is the minimum amount of data - obviously more fields are ok */
    "staticTree": [
        {"id":1, "label":"testxsx", "expanded": true, "children":[
          {"id":4, "label":"sub1", "isSelected":true, "expanded": true},
          {"id":5, "label":"sub2", hidden: false, "expanded": false, "children":[
            {"id":7, "label":"sub1", hidden: false, "expanded": true},
            {"id":8, "label":"sub2", "expanded": false},
            {"id":9, "label":"sub3", "expanded": false},
          ]},
          {"id":6, "label":"sub3", hidden: false, "expanded": false},
        ]},
        {"id":2, "label":"test2", "expanded": false},
        {"id":3, "label":"test3", "expanded": true, "children":[
            {"id":10, "label":"sub1", "expanded": false},
            {"id":11, "label":"sub2", "expanded": false},
            {"id":12, "label":"sub3", "expanded": false},
        ]},
      ],
      "emptyTree": [], //used as a placeholder while loading data
      "currentStatus": "loading...", //empty string if all is well/okay
      "readyToDisplay": false,
      "status":"",
      "pageList":[],
      "useStaticTree": false,
      "testx": 1
  },
  getters: {
    /*
    getSelected(state) {
      //debugger // eslint-disable-line no-debugger
      //var currentDepth=0;
      //var found=false;
      if (state) {
        var trg=wpPages.findPage(state.activeMenuId);
        if (trg) {
          console.log("current title: " + trg.obj.label)
          state.expandedIds=trg.path
          //debugger // eslint-disable-line no-debugger
        } else {
          store.dispatch('app/addError',{"msg":"current menu item not found (id: " + state.activeMenuId + ")"})
          //console.log("Error: current menu item not found (id: " + state.activeMenuId + ")")
        }
      }
      return state.activeMenuId
    }
    */
  },
  computed: mapState({
    /*
    test (state) {
      //debugger // eslint-disable-line no-debugger
      if (state) {
        console.log("yay - ok")
      }
      return state.activeMenuId
    }*/
  }),
  data: {
      pageArray: [], /* non reactive store of pages */
  },

  //find item with attributes: {att:"attribute id", val:attribute value} e.g. find item with id=1 ={att:"id", val:1}
  //returns {obj:<found object>, path:<path to object (id's)> }
  findPage(findAtts) {
    //console.log("findPage:"+findAtts);
    
    //debugger // eslint-disable-line no-debugger
    
    if (!this.state.pageList || this.state.pageList.length<1) {
      debugger // eslint-disable-line no-debugger
      store.dispatch('app/addError',{"msg":"Error! - cannot find a page in a non-existant list!"})
      return undefined
    } else {
      var found;
      var path=[];
      var trg=this.state.pageList.children
      for (var i in trg) {
        var ret=this.findPageCheckNode(trg[i], findAtts, path)
        if (ret) {
          //debugger // eslint-disable-line no-debugger
          found=ret;
          break;
        } else {
          path.pop()
        }
      }
    }
    //debugger // eslint-disable-line no-debugger
    return found
    
  },
  findPageCheckNode(obj, findAtts, path) {
    path.push(obj.id)
    //console.log("checkNode: " + obj.id)
    //debugger // eslint-disable-line no-debugger
    if (obj.children) {
      for (var i in obj.children) {
        var r=this.findPageCheckNode(obj.children[i], findAtts, path)
        if (r) {
          //debugger // eslint-disable-line no-debugger
          return r
        } else {
          path.pop()
        }
      }
    }
    if (obj[findAtts.att]===findAtts.val) {
      //debugger // eslint-disable-line no-debugger
      return {obj:obj, path: path}
    }
  },


  pageWalk() {
    //console.log("pageWalk")
    //debugger // eslint-disable-line no-debugger
    var ordered=this.getLevel(WPnavMenu.data.pageArray, 0)
    //order level 0
    ordered=this.orderByMenuOrder(ordered);
    //console.log(ordered)
    //debugger // eslint-disable-line no-debugger
    

    //this.state.pages=ordered
    //this.state.pages.push(...ordered)
    //debugger // eslint-disable-line no-debugger
    
    //this.commit("correctPageOrdering2")
    
    for (var i=0;i<ordered.length;i++) {
      //debugger // eslint-disable-line no-debugger
      ordered[i]=this.getChildren(ordered[i], -1)
    }
    
    return ordered
  },

  getChildren(dat, currentDepth) {
    currentDepth++
    dat.adepth=currentDepth
    var currentParent=dat.id
    var hasChildren=true
    var children
    while (hasChildren) {
      children=this.getLevel(WPnavMenu.data.pageArray, currentParent)
      if (children.length>0) {
        //console.log(children)
        //debugger // eslint-disable-line no-debugger
        for (var i=0;i<children.length;i++) {
          children[i].children=this.getChildren(children[i], currentDepth)
        }
        dat.children=children
      } else {
        console.log(currentParent + " has no children (WPnavMenu.js)")
        hasChildren=false
      }
    }
    //debugger // eslint-disable-line no-debugger
    return dat
    
  },


  getLevel(arr, id) {
    var ret=[]
    var len=arr.length;
    for (var i=0;i<len;i++) {
      if (arr[i].parent===id) {
        ret.push(...arr.splice(i,1))
        len--;i--;
      }
    }
    return ret
  },
  orderByMenuOrder(arr) {
    arr.sort(function(a, b){
      //debugger // eslint-disable-line no-debugger
      if (a.menu_order < b.menu_order) {return -1;}
      if (a.menu_order > b.menu_order) {return 1;}
      return 0;
    });
    return arr;
  },


  mutations: {
    addToList(state, dat) {
      //console.log(state + " : " + dat)
      state.pages.push(...dat)
      //debugger // eslint-disable-line no-debugger
    },

    correctPageOrdering2(state) {
      state.pages=WPnavMenu.data.pageArray
      if (state) {
        //console.log("ok")
      }
    },

    replacePageMenu(state, dat) {
      if (dat) {
        //debugger // eslint-disable-line no-debugger  
        //var top={label: "dummyTopLevelItem", hidden: false, "showChildren": true, "expanded": true, children:[]}
        //top.children=dat

        state.pageList=dat

        //state.pageList.children=dat

        //this is how we tell the front end that we are ready
        state.currentStatus=""
        state.readyToDisplay=true


        //Object.assign(top.children, ret); //old skool
      }
      //debugger // eslint-disable-line no-debugger
    }
    
  },

  actions: {

    testCall() {
      //debugger // eslint-disable-line no-debugger
    },

    setCurrentPage({dispatch},node) {
      //debugger // eslint-disable-line no-debugger
      //console.log(msg)
      //find any currently selected item and set to false
      var trg=WPnavMenu.findPage({att:"isSelected", val:true});
      if (trg) {
        trg.obj.isSelected=false
      }
      trg=WPnavMenu.findPage({att:"id", val:node.obj.id});
      //debugger // eslint-disable-line no-debugger
      if (trg) {
        console.log("found title: " + trg.obj.label)
        trg.obj.isSelected=true
        //trg.obj.expanded=true
        trg.obj.showChildren=true
        //debugger // eslint-disable-line no-debugger
        
        dispatch('WPcontent/navigateTo', node.obj.origData, {root:true});
        
        

      } else {
        console.log("NOT FOUND!")
      }
    },

    logError({dispatch}, msg) {
      debugger // eslint-disable-line no-debugger
      dispatch('app/addError', {"msg":"Error! - " + msg}, {root:true})
    },


    async init({ state, dispatch, commit}, params) {
      console.log("WPnavMenu - init")
      

      if (params) {
        //debugger // eslint-disable-line no-debugger
        if (params.site) {
          state.wordPressSite=params.site
        }
        if (params.pagesEndpoint) {
          state.pagesEndpoint=params.pagesEndpoint
        }
      }
      


      if (state.wordPressSite!="") {
        //console.log(commit + state)

        //note how we add a dummy top level that will be filtered out on display
        var top={label: "dummyTopLevelItem", hidden: false, "showChildren": true, "expanded": true, children:[]}
        
        //top.children={}
        if (state.useStaticTree) {
          top.children=state.staticTree
          state.pageList=top
          //this is how we tell the front end that we are ready
          state.currentStatus=""
          state.readyToDisplay=true
          
          return true
        } else {

          //note how we DO NOT wait for this!
          loadWPcontentsList.loadWPcontents(state.wordPressSite+state.pagesEndpoint).then(
            function (ret) {
              
              var t=reactive(ret);
              top.children=t
              commit('replacePageMenu', top)

              
              //note how its necessary to create/replace a reactive object - just putting the array in there does not work!
              //state.pageList=top

              //top.children=state.pageList
              //debugger // eslint-disable-line no-debugger
              /*
              Object.assign(top.children, ret); //old skool
              state.pageList=top
              state.currentStatus=""
              state.readyToDisplay=true
              */
              
              /*
              state.pageList=top
              state.currentStatus=""
              state.readyToDisplay=true
              */
              
              //top.children=reactive(ret)
              //state.pageList=top
              //debugger // eslint-disable-line no-debugger
              if (ret) {
                //console.log("loadWPcontents ok")
              }
            },
            function (err) {
              //debugger // eslint-disable-line no-debugger
              
              if (err) {
                console.log("loadWPcontents fail")
                dispatch('utils/addError',{msg:'WPnavMenu : ' + err}, { root: true })
              }
            }
          )
          //debugger // eslint-disable-line no-debugger
          return true

        }
      } else {
        dispatch('utils/addError',{msg:'WPnavMenu : no WP site defined!'}, { root: true })
      }


    },


  },
  modules: {
    loadWPcontentsList: loadWPcontentsList,
  }

}