
/* 

getData will load paged wordpress contents data - it calls loadData obj

*/

import { loadData } from './loadData.js'
export const getData = {
  namespaced: true,

  test() {
    console.log("huh")
  },

  state: {
    iam: "",
  },

  data: {
    url:"",
    pages:[],
    maxPages:20, //limiter on amount of request that will be made inside the 'loadPagedRecords' loop
    allDataLoaded:false, //if server responds with no more data then this is set
    pagedRequestArray:[],
    currentAmountPerPageToLoad:0,
    maxPageToLoadWorking:0,
    loadingPages:false,
    loadingData:false,
    itemsToLoad:-1
  },
  

  wait3() {
    return new Promise((resolve, reject) => {
      console.log("....wait3 start")
      setTimeout(() => {
        console.log("....wait3 done")
        debugger // eslint-disable-line no-debugger
        resolve("3 second wait done")
      }, 3000)
      if (reject) {
        console.log("x")
      }
    })
  },


//    ={{resultsPerPage}}&order=asc&page={{maxPageLoaded}}


  //returns {dat: records, thereIsMore: bool}
  //url has to be in format: <snip>?per_page={{resultsPerPage}}&order=asc&page={{maxPageLoaded}}
  async loadPagedRecordsX(url, startPage, maxPageToLoad, amountPerPage=10, functionToCall) {
    //console.log("loadPagedRecords: " + startPage + " :: " + maxPageToLoad)
    this.loadingPages=true
    this.loadingData=true
    this.data.itemsToLoad=maxPageToLoad*amountPerPage;
    return new Promise((resolve) => {
      
      var errored=""
      if (maxPageToLoad>0) {
        //debugger // eslint-disable-line no-debugger
        //pageStart = Math.floor(start/amountPerPage)
        if (startPage<1) {startPage=1}

        getData.data.currentAmountPerPageToLoad=amountPerPage
        
        //debugger // eslint-disable-line no-debugger
        var u
        while (startPage<maxPageToLoad+1) {
          u=url.replace("{{resultsPerPage}}",amountPerPage)
          u=u.replace("{{maxPageLoaded}}", startPage)
          
          getData.data.pagedRequestArray.push(u)
          startPage++
        }

        if (getData.data.pagedRequestArray.length>getData.data.maxPages) {
          getData.data.pagedRequestArray=getData.data.pagedRequestArray.slice(0,getData.data.maxPages)
          errored="loadPagedRecords was asked to load more than maxPages allows!"
        } else {
          this.loadNext(functionToCall)
        }
        //debugger // eslint-disable-line no-debugger
        
      }

      if (errored==="") {
        resolve({"status":"loading..."})
      } else {
        resolve({"error":errored})
      }
      
    })
  },


  async loadPagedRecords(url, startPage, maxPageToLoad, amountPerPage=10) {

    const loadDataObj=loadData

    //console.log(url, startPage, maxPageToLoad, amountPerPage);
    //console.log("loadPagedRecords: " + startPage + " : " + maxPageToLoad)

    this.data.itemsToLoad=maxPageToLoad*amountPerPage

    var moreToLoad=true
    var err=""
    startPage--;
    while (moreToLoad) {
      startPage++;
      if (startPage>maxPageToLoad) {
        //we are done loading!
        moreToLoad=false
      }

      if (moreToLoad) {
        var u=url.replace("{{resultsPerPage}}",amountPerPage)
        u=u.replace("{{maxPageLoaded}}", startPage)
        //console.log("load: " + u)


        await loadDataObj.loadData(u).then(
          function (dat) {
            if (dat.error) {
              moreToLoad=false
              err=dat.error
            } else {
              
              //data returned ok... check...
              if (dat.status==="ok") {
                //debugger // eslint-disable-line no-debugger
                //header check ... for example this might be a wordpress request where we can check total number of pages requested
                var ret=getData.checkResponseHeaders(dat.obj.headers, maxPageToLoad)
                if (ret.maxPageToLoad) {
                  maxPageToLoad=ret.maxPageToLoad
                }


                if (!ret.error) {
                  if (dat.obj.data) {
                    getData.data.pages.push(...dat.obj.data)
                  }
                } else {
                  moreToLoad=false
                  err=ret.error
                }
              }


            }
            //debugger // eslint-disable-line no-debugger
        })
        //debugger // eslint-disable-line no-debugger
      }
      

    }

    if (err!="") {
      console.log(err)
    }

    //debugger // eslint-disable-line no-debugger

    return new Promise(function(loaded, failed) {
      //debugger // eslint-disable-line no-debugger
      if (err==="") {
        var ret={}
        ret.status="ok"
        ret.data=getData.data.pages
        //debugger // eslint-disable-line no-debugger
        loaded(ret)
      } else {
        failed(err)
      }
    })
  },

  checkResponseHeaders(headers, maxPageToLoad) {
    //debugger // eslint-disable-line no-debugger
    var am
    var ret={}
    if (headers['x-wp-totalpages']) {
      am=parseInt(headers['x-wp-totalpages'])
      if (am<maxPageToLoad) {
        console.log("amount of pagedRequests has been reduced because the server says there are less!")
        ret.maxPageToLoad=am
      }
    }
    if (headers['x-wp-total']) {
      am=parseInt(headers['x-wp-total'])
      //debugger // eslint-disable-line no-debugger
      if (am>this.data.itemsToLoad) {
        ret={"error":"Not all of the WP contents list is being loaded! - MUST load it all to display correctly! (" + headers['x-wp-total'] + " available. Told to load: " + this.data.itemsToLoad +")"}
      }
    }
    //debugger // eslint-disable-line no-debugger
    return ret
  },
  

  loadNext(functionToCall) {
    const loadDataObj=loadData
    //debugger // eslint-disable-line no-debugger
    this.data.loadingData=true
    //this.loadData(getData.data.pagedRequestArray[0]).then(
    loadDataObj.loadData(getData.data.pagedRequestArray[0]).then(
      function (dat) {
        
        if (dat) {
          //debugger // eslint-disable-line no-debugger
          var ret=undefined
          if (dat.status) {
            if (dat.status==="finished") {
              getData.data.loadingData=false
              getData.data.pagedRequestArray=[]
              ret={"status":"ok"}
            } else {
              if (dat.status==="ok") {

                //header check ... for example this might be a wordpress request where we can check total number of pages requested
                ret=getData.checkResponseHeaders(dat.obj.headers)

                if (!ret.error) {
                  if (dat.obj.data) {
                    getData.data.pages.push(...dat.obj.data)
                  }
  
                  getData.data.pagedRequestArray.shift()
                  if (getData.data.pagedRequestArray.length>0) {
                    //debugger // eslint-disable-line no-debugger
                    getData.loadNext(functionToCall)
                  } else {
                    //all done
                    getData.data.loadingData=false
                    ret={"status":"ok"}
                  }
                }
              }
            }
          } else {
            if (dat.error) {
              //debugger // eslint-disable-line no-debugger
              getData.data.loadingData=false
              getData.data.pagedRequestArray=[]
              ret={"error":dat.error}
              //return false
            }
          }
          //debugger // eslint-disable-line no-debugger    
          if (ret!=undefined) {
            //here is we have been sucessful
            if (ret.status==="ok") {
              ret.data=getData.data.pages
              
              getData.data.loadingPages=false
              functionToCall(ret)
            } 
            if (ret.error) {
              functionToCall(ret)
            }
            //debugger // eslint-disable-line no-debugger    
            
          }
          //debugger // eslint-disable-line no-debugger    
        }
      }
    )
  },
  
}

