/* AXIOS */
/* https://vuejs.org/v2/cookbook/using-axios-to-consume-apis.html */
/* https://github.com/axios/axios */


import axios from 'axios'
export const loadData = {
  namespaced: true,

  test() {
    console.log("huh")
  },

  state: {
    iam: "loadData",
    auth: ""
  },


  /*
  plugin: JWT Authentication for WP-API

  */

  async getJWT(url) {

    return new Promise((resolve) => {
      axios ({
          method: 'post',
          //headers: { 'Access-Control-Request-Headers': 'content-type' },
          url: url+'/?rest_route=/jwt-auth/v1/token',
          data: {
            username: 'test',
            password: '#rW7NGnYAwSj1NElq#6jadJL',
          }
        })
        .then((res) => {
          
          /*
          if (sessionStorage.getItem('jwt_token') === null) {
            sessionStorage.setItem('jwt_token', res.data.token);
          } else {
            sessionStorage.removeItem('jwt_token');
            sessionStorage.setItem('jwt_token');
          }*/
          console.log(res)

          //res will contain a whole load of info including token, username etc.
          
          debugger // eslint-disable-line no-debugger
          resolve({"ok":"ok"})
          
        })
        .catch((err) => {
          var msg="Load error - check console"
          if (err.message) {
            msg=err.message
            if (msg==="Network Error") {
              msg+=" (possible CORS error)"
            }
          }
          if (err.message==="Request failed with status code 404") {
            msg+=" (maybe the authentication plugin is not installed)"
            //https://wordpress.org/plugins/jwt-authentication-for-wp-rest-api/
          }
          if (err.response) {
            if (err.response.statusText) {
              msg+="<br>"+err.response.statusText
            }
          }
          debugger // eslint-disable-line no-debugger
          resolve({"error":msg})
        })
    })
  },

  async loadData(url) {

    return new Promise((resolve) => {
      //var data
      axios
      .get(url)
      .then(response => {
         //debugger // eslint-disable-line no-debugger
         if (response) {
          //debugger // eslint-disable-line no-debugger
          resolve ({"status":"ok", "obj":response})
         }
      })
      .catch(error => {
        //console.log(error)
        debugger // eslint-disable-line no-debugger
        //assume that 'no more pages available' is not actually an error
        if (error.response) {
          if (error.response.data.message==="The page number requested is larger than the number of pages available.") {
            error.response.data=undefined
  
            resolve({"status":"finished", "obj":error.response})
          } else {
            if (error.response.data.message) {
              resolve({"error":error.response.data.message})
            } else {
              resolve({"error":error.message})
            }
          }
        } else {
          if (error.message) {
            if (error.message==="Network Error") {
              resolve({"error":"Load error: please check the URL / Network connection!"})
            } else {
              resolve({"error":error.message})
            }
            
          } else {
            resolve({"error":"Load error - please check console."})
          }
          
        }
      })
    })
  },

  wait3() {
    return new Promise((resolve, reject) => {
      console.log("....wait3 start")
      setTimeout(() => {
        console.log("....wait3 done")
        debugger // eslint-disable-line no-debugger
        resolve("3 second wait done")
      }, 3000)
      if (reject) {
        console.log("x")
      }
    })
  },  
}






