export const utils= {
  namespaced: true,
  state: {
    "iam": "utils.js (store)",
    "ready": false,
    "errorMessage": "",
  },


  mutations: {
    ready(state) {
      state.ready=true;
    }
  },

  actions: {
    //async init({ commit, dispatch }) {
    //async init({ commit , state, dispatch}) {
    init({ dispatch }) {

      //td: check required has loaded...
      //commit('app/addDebugMessage','utils (init)', { root: true })
      dispatch('addDebugMessage', 'utils (init)')

      //debugger // eslint-disable-line no-debugger
      //utils.objs.dispatch=dispatch
      return true
    },

    //adds to an array of messages
    addDebugMessage({ commit }, msg) {
      commit('app/addDebugMessage',msg, { root: true })
    },

    //obj is and object! obj.message
    addError({ dispatch }, obj) {
      if (!obj.heading) {
        obj.heading="Error"
      }
      dispatch('app/addError', obj, { root: true })
      //commit('app/addDebugMessage','Error: ' + obj.msg)
      //debugger // eslint-disable-line no-debugger
    },

  },
}