/* 

should contain global display functions/arrays. 
ACCESSED/CHANGES via utils

app.vue looks at vars in here

e.g. error displays, debug etc

*/


//import { lang } from './lang.js'

export const app= {
  namespaced: true,
  state: {
    "iam": "app.js",
    "test": "blah",
    "ready": false,
    "dynamic":{}, /* dynamic populated during runtime - so dont add any here! */
    //"version": 0.1,
    "isMessageScreenVisible":true, /*starts in show mode because it has a loading message */
    "messageScreen": {
      "heading": "Loading",
      "content": "Please wait...",
    },
    "errored": false,
    "errors": [],
    "debugIsVisible":false,
    "debug": true,
    "debugScreenContent": [
    ],
    "allowDebugScreen": true,
    init () {
      console.log("init")
    },
  
  },

  mutations: {
    getDynamic(state, obj) {
      debugger // eslint-disable-line no-debugger
      if (state.dynamic[obj.id]==undefined) {
        if (obj.default==undefined) {
          //if this error shows then a code refactor is probably needed somewhere!
          console.error("store.js: tried to access a dynamic store value (" + obj.id + ") that does not actually exist and no default value was supplied - this is propbably a sequencing/timing issue!");
        } else {
          state.dynamic[obj.id]=obj.default;
        }
      }
      return state.dynamic[obj.id];
    },

    showDebug(state) {
      state.debugIsVisible=!state.debugIsVisible
    },

    showMessageScreen(state, obj) {
      /*
      setTimeout(() => {
        state.isMessageScreenVisible=obj.truefalse;
      }, 3000)
      */
      //debugger // eslint-disable-line no-debugger
      state.isMessageScreenVisible=obj.truefalse;
      if (obj.heading) {
        state.messageScreen.heading=obj.heading;
      }
      if (obj.content) {
        state.messageScreen.content=obj.content;
      }
    },
    addDebugMessage(state, msg) {
      const tm=new Date().toLocaleTimeString()
      state.debugScreenContent.push({'id':state.debugScreenContent.length+1,'tm': tm, 'text':msg})
    },

    init(state) {
      console.log("app.js init")
      state.ready=true
    }

  },


  actions: {
    async init({ commit }) {
      var allOkay=true
      if (allOkay) {
        commit("init")
        return true
      } else {
        return false;
      }
    },

    showDebug({ commit }) {
      commit('showDebug')
    },
    showMessageScreen({ commit }, obj) {
      commit('showMessageScreen', obj)
    },

    addError({ state, commit }, obj) {
      state.errored=true
      state.errors.push(obj.msg)
      var str=state.errors.join("<br>")
      commit('showMessageScreen', {truefalse:true, heading:'Error', content:str})
      commit('addDebugMessage','Error: ' + obj.msg)
      debugger // eslint-disable-line no-debugger
    },
    
  },
  modules: {
  }

}