export const lang= {
  namespaced: true,
  state: {
    "iam": "lang.js",
    "ready": false,
    "text": {
      "welcomeDescription":"blah"
    },
    "errorMessage": "",
    "XlangFileToLoad": "https://loopback.hopto.me/json/lang.json", //"https://loopback.hopto.me/json/data.php",
    "langFileToLoad": "https://www8.uis.no/fag/vue/externalLoads/lang.json",
    "defaultLang": "en_gb",
    //"defaultLang": "no_nb",
    "languagesAvailable": [],
    "loadedTexts": [] /* populated by json load */
  },

  mutations: {
    dataLoaded(state, dat) {
      state.loadedTexts=dat
      for (var i=0;i<state.loadedTexts.length;i++) {
          state.languagesAvailable.push(state.loadedTexts[i].lang)
      }
    },

    setLanguage(state, index) {
      //copy default lang texts into state.text
      //debugger // eslint-disable-line no-debugger
      state.text=state.loadedTexts[index].texts
      //debugger // eslint-disable-line no-debugger
    },

    ready(state) {
      state.ready=true;
    }

  },

  actions: {
    //async init({ commit, dispatch }) {
    async init({ commit , state, dispatch}) {
      console.log("lang init - loadData")
      return fetch(state.langFileToLoad)
      .then((res)=>res.json())
      .then((data) => {
        commit('dataLoaded', data)
        var langWasSet=dispatch('setLanguage', state.defaultLang).then((ret) => {
          //debugger // eslint-disable-line no-debugger
          if (ret) {
            commit('ready')
            return true
          } else {
            debugger // eslint-disable-line no-debugger
            dispatch('utils/addError', {"msg":"Failed to set default language texts: " + state.errorMessage})
            return false
          }
        })
        return langWasSet
      })
      .catch((err) => {
        //debugger // eslint-disable-line no-debugger
        dispatch('app/addError', {"msg":"Failed to load language texts file: " + err.message}, {root:true})
        state.errorMessage=err.message
        return false
      })
    },

    setLanguage({ commit, state }, id) {
      commit('app/addDebugMessage', "Set language: " + id, {root:true})
      var index=undefined;
      for (var i=0;i<state.loadedTexts.length;i++) {
        if (state.loadedTexts[i].lang===id) {
          index=i;
          break;
        }
      }
      //debugger // eslint-disable-line no-debugger
      if (index!=undefined) {
        commit('setLanguage', index)
        return true
      } else {
        state.errorMessage="No language texts found for: " + id
        return false
      }
    },
  },
}