
/* 
WPcontent is the outer object while contains:
WPpage - object for loading a page
WPnav - object for loading navigation
*/

//import { WPpage } from './WPpage/WPpage.js'
import { WPnavMenu } from './WPnavMenu/WPnavMenu.js'
import { WPpage } from './WPpage/WPpage.js'
import { loadData } from '../helpers/loadData.js'

//this.dispatch('navMenu/init')

export const WPcontent= {
  namespaced: true,
  state: {
    "iam": "WPcontent.js",
    "ready": false,
    "store":"",
    "dispatch":"",
    "wordpressSite":"https://mooc.conductit.eu",
    //"wordpressSite":"https://digjazz.no",
    //"wordpressSite":"https://leigh.algol.xyz",
    //"wordpressSite":"https://loopback.hopto.me",

    "pagesEndpoint": "/wp-json/wp/v2/pages/?per_page={{resultsPerPage}}&page={{maxPageLoaded}}",
    //"pagesEndpoint": "/?rest_route=/wp/v2/pages/&per_page={{resultsPerPage}}&page={{maxPageLoaded}}", //old skool! 


    //these are work in progress (TD) - username&pass are validated - but need to store token etc - see loadData->getJWT
    "username": "",
    "password": "",


  },

  mutations: {
    ready(state) {
      state.ready=true;
    }
  },

  actions: {


    navigateTo({ dispatch },node) {
      if (node) {
        //console.log("WPcontent -> navigateTo")
        //debugger // eslint-disable-line no-debugger
        dispatch('WPcontent/WPpage/loadPage', node, { root: true })  
      }
      
      
    },


    async init({ dispatch, state }) {
      var proceed=true
      if (state.username!="" || state.password!="") {
        //debugger // eslint-disable-line no-debugger     
        //const [WPnavMenuInit, WPpageInit] = await Promise.all([this.dispatch('WPcontent/WPnavMenu/init',{"site":state.wordpressSite}), this.dispatch('WPcontent/WPpage/init',{"site":state.wordpressSite})]);  
        
        await loadData.getJWT(state.wordpressSite).then((res) =>
          {
            debugger // eslint-disable-line no-debugger       
            if (res.error) {
              dispatch('utils/addError',{msg:res.error}, { root: true })
              proceed=false
            } 
            //console.log(res)
          }
        )
        debugger // eslint-disable-line no-debugger     
        //return true
      } 
      if (proceed) {
        //debugger // eslint-disable-line no-debugger     
        var ret=await dispatch('initDo') 
        //debugger // eslint-disable-line no-debugger     
        return ret
      }
    },

    //async init({ commit, dispatch }) {
    //async init({ commit , state, dispatch}) {
    async initDo({ dispatch, state }) {
      //debugger // eslint-disable-line no-debugger     
      /*
      function showError(msg) {
        dispatch('app/addError',{msg:msg}, { root: true })
      }*/





      //this.dispatch('app/addError',{msg:"msg"})
      //console.log("WPcontent - init")
      dispatch('utils/addDebugMessage',"WPcontent - init", { root: true })

      var ret=true

      //const [loadLang, loadPages] = await Promise.all([this.dispatch('app/init',{"test":"blah"}), this.dispatch('wpPages/init')]);
      //const [loadLang, wait3] = await Promise.all([this.dispatch('app/init',{"test":"blah"}), this.dispatch('doNothingButWait/wait3')]);


      const [WPnavMenuInit, WPpageInit] = await Promise.all([this.dispatch('WPcontent/WPnavMenu/init',{"site":state.wordpressSite, "pagesEndpoint":state.pagesEndpoint}), this.dispatch('WPcontent/WPpage/init',{"site":state.wordpressSite})]);
      debugger // eslint-disable-line no-debugger     
      if (!WPnavMenuInit) {
        //debugger // eslint-disable-line no-debugger     
        dispatch('utils/addError',{msg:'WPnavMenu failed.'}, { root: true })
        ret=false
      }
      if (!WPpageInit) {
        //debugger // eslint-disable-line no-debugger     
        dispatch('utils/addError',{msg:'WPpage failed.'}, { root: true })
        ret=false
      }

      /*
      const [WPpageInit] = await Promise.all([this.dispatch('WPcontent/WPpage/init',{"test":"blah"})]);
      if (!WPpageInit) {
        //debugger // eslint-disable-line no-debugger     
        dispatch('utils/addError',{msg:'WPpageInit failed.'}, { root: true })
        ret=false
      }
      */
      return ret      
    },

  },
  modules: {
    WPpage: WPpage,
    WPnavMenu: WPnavMenu
  }
}