//import { WPcontent } from "../WPcontent";

import { loadData } from '../../helpers/loadData.js'

export const WPpage= {
  namespaced: true,
  state: {
    "iam": "WPpage.js",
    "ready": false,
    "contentArray": [
      {"id":1, "src":"dummy", "contents":"hi this is a test"}
    ],
    "errorMessage": "",
    "wordPressSite":"",
    "currentPageData":"nothing to see here...", //has the html of the current page
    "currentPageObj":undefined,

    "readyToDisplayPage": false,


    "pageEndpoint": "/?rest_route=/wp/v2/pages/{{pageId}}", //pretty links NOT enabled
    "pageEndpointX": "/wp/v2/pages/{{pageId}}", //pretty links enabled
    "test": 0
  },

  mutations: {
    ready(state) {
      state.ready=true;
    }
  },

  async loadPage(url) {
    const loadDataObj=loadData
    if (url) {
      var page=await loadDataObj.loadData(url).then(
        function (dat) {
          if (dat) {
            //debugger // eslint-disable-line no-debugger
            return dat.obj.data.content.rendered
            
          }
          
        }
      )     
      return page
    }
  },

  actions: {

    async loadPage({state, dispatch},obj) {

      if (obj) {
        
        state.currentPageObj=obj
        
        var url=state.pageEndpoint
        url=url.replace("{{pageId}}", obj.id)
        url=state.wordPressSite+url
        //console.log("loadPage: " + url)
        
        var pageDat= await WPpage.loadPage(url)
        if (pageDat) {
          //debugger // eslint-disable-line no-debugger
          state.currentPageData=pageDat
          state.readyToDisplayPage=true
        } else {
          dispatch('utils/addError',{msg:'WPpage load failed.'}, { root: true })
        }
        //debugger // eslint-disable-line no-debugger
      }
      
    },


    //async init({ commit, dispatch }) {
    //async init({ commit , state, dispatch}) {
    async init({ dispatch, state }, params) {
      //debugger // eslint-disable-line no-debugger
      

      if (params) {
        //debugger // eslint-disable-line no-debugger
        if (params.site) {
          state.wordPressSite=params.site
        }
      }


      if (state.wordPressSite!="") {
        dispatch('utils/addDebugMessage',"WPpage - init", { root: true })
      } else {
        dispatch('utils/addError',{msg:'WPpage : no WP site defined!'}, { root: true })
      }


      dispatch('utils/addDebugMessage',"WPpage - init", { root: true })
      return true
    },

  },
}