import { createStore } from 'vuex'
//import { mapState } from 'vuex'
import { app } from './data/app.js'
//import { navMenu } from './data/navMenu.js'
import { doNothingButWait } from './data/doNothingButWait.js'
import { WPcontent } from './data/wordpress/WPcontent.js'
import { utils } from './utils.js' //common functions e.g. debug message display
import { lang } from './data/lang.js'


export default createStore({
  state: {
    "iam": "store",
    "storeIsReady": false,
    "dynamic":{}, /* dynamic populated during runtime - so dont add any here! */
  },
  mutations: {
    init(state) {
      state.storeIsReady=true;
      console.log("...store init is done!")
    }

  },

  actions: {
    //init({ commit }) {
    async init({ commit, dispatch}) {
      var allOkay=true
      
      
      //debugger // eslint-disable-line no-debugger     
      const [utilsOkay, appJSOkay] = await Promise.all([this.dispatch('utils/init'), this.dispatch('app/init')]);
      //const [loadLang, loadPages] = await Promise.all([this.dispatch('app/init',{"test":"blah"}), this.dispatch('wpPages/init')]);
      //const [loadLang, wait3] = await Promise.all([this.dispatch('app/init',{"test":"blah"}), this.dispatch('doNothingButWait/wait3')]);

      if (utilsOkay && appJSOkay) {
        const [loadLang] = await Promise.all([dispatch('lang/init')]);
        if (loadLang) {
          dispatch('utils/addDebugMessage','Program started')
          //debugger // eslint-disable-line no-debugger
          
          //const [loadPages, WPcontentInit] = await Promise.all([this.dispatch('navMenu/init'), this.dispatch('WPcontent/init', {"utils":utils})]);
          
          //const [loadLang, loadPages] = await Promise.all([this.dispatch('app/init',{"test":"blah"}), this.dispatch('wpPages/init')]);
          //const [loadLang, wait3] = await Promise.all([this.dispatch('app/init',{"test":"blah"}), this.dispatch('doNothingButWait/wait3')]);

          
          const [WPcontentInit] = await Promise.all([this.dispatch('WPcontent/init', {"utils":utils})]);
          if (!WPcontentInit) {
            allOkay=false
          }
          
        } else {
          dispatch('utils/addError',{msg:"Could not load language (store.js)"})  
          allOkay=false
        }
        
      } else {

        var msg="";
        if (!utilsOkay) {
          msg="Could not start utils.js (store.js)"
        }
        if (!appJSOkay) {
          msg="Could not start app.js (store.js)"
        }
        debugger // eslint-disable-line no-debugger     
        dispatch('app/addError',{msg:msg}, { root: true })
        allOkay=false
      }



      
      if (allOkay) {
        /*
        if (!store.state.app.errored) {
          store.dispatch('app/showMessageScreen', {truefalse: false})
          return true
        } else {
          return false
        }
        */
        commit('init')
        return true
      } else {
        return false
      }
      //commit('init')

      
    },


/////////////////////////////////////
/* 
example calls:

          store.dispatch('init4').then((ret) => {
            //debugger // eslint-disable-line no-debugger
            if (ret) {
              console.log("app.vue store is ready");
              store.dispatch('app/showMessageScreen', {truefalse: false})
            } else {
              console.error("store failed - returned false");
              store.dispatch('app/showMessageScreen', {truefalse: true, heading:"Error", content:'Failed to start - <a href="./">Click to retry</a>'})
            }
          });

*/
    //fire and forget - do not wait
    async init4({ commit }) {
      this.dispatch('doNothingButWait/wait3')
      this.dispatch('doNothingButWait/wait6')
      commit('init')
      return true
    },


    //non parallel - sequential wait
    async init2({ commit }) {
      await this.dispatch('doNothingButWait/wait3')
      await this.dispatch('doNothingButWait/wait6')
      commit('init')
      return true
    },

    //parallel processing WITH wait
    async init3({ commit }) {
      const [wait3, wait6] = await Promise.all([this.dispatch('doNothingButWait/wait3'), this.dispatch('doNothingButWait/wait6')]);
      console.log(wait3);
      console.log(wait6);
      /*
      if (someResult) {
        commit('init')
      }*/
      commit('init')
      return true
    }
////////////////////////////////////



    //debugger // eslint-disable-line no-debugger    

    /*
    showDebug(state) {
      console.log("x")
      state.dynamic.debugIsVisible=!state.dynamic.debugIsVisible
    }*/

    //get a 'dynamic' state var - if it does not exist then creates and sets to defaultValue 
  },
  modules: {
    app: app,
    utils: utils,
    doNothingButWait: doNothingButWait,
    //navMenu: navMenu,
    WPcontent: WPcontent,
    lang:lang
  }
})
